label{
    position: absolute;
    right: 8rem;
    top: 1.6rem;
}
switch {
    position: relative;

  }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #A172EF;
    transition: .4s;
    width: 6rem;
    height: 3.2rem;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #FD7B7B;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #FD7B7B;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }


@media  (max-width: 480px){
    label{
        right: 6.2rem;

    }
}

@media  (max-width: 350px){
  .slider{
    height: 3rem;
  }
  label{
    top: 1rem;
}
.slider:before {
  height: 23px;
  width: 23px;
}
}

